/* sets everything to defualt settings  */
@import url('https://fonts.cdnfonts.com/css/futura-md-bt');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* variables for the website for it's style */
:root {
    --color-bg: #00001a;
    --color-bg-variant: #1c155f;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255 , 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-ms: 90%;
}

html {
    /* makes the website scroll smoothly */
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    /* display: none; */
    width: 10px;
}

::-webkit-scrollbar-track {
    background: rgb(0, 0, 41);
}

::-webkit-scrollbar-thumb {
    background: cyan;
    border-radius: 1rem;
}

body {
    font-family: 'Futura Lt BT', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url("./assets/gplay.png");
    overflow: hidden;
}

/* general styles */
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;   
}

/* the arrows indicate the h2, h5 in sections */
section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
    text-transform: uppercase;
    letter-spacing: 4px;
    font-weight: 800;
}

.text-light { 
    color: var(--color-light)
}

a {
    color: var(--color-primary);
    transition: val(--transition);
}

a:hover {
     color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg)
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/* media queries (medium devices) */

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* media queries (small devices) */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}


.loader-wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--color-bg);
    /* display: flex;
    justify-content: center;
    align-items: center; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 2;
}


.loader {
    margin-top: 40px;
    display: inline-block;
    width: 90px;
    height: 90px;
    position: relative;
    border: 4px solid var(--color-primary);
    
    animation: loader 2s infinite ease;
    border-radius: 20px;
  }
  
  .loader-inner {
    vertical-align: top;
    display: inline-block;
    width: 100%;
    background-color: var(--color-primary);
    animation: loader-inner 2s infinite ease-in;
    background: var(--color-primary);
    border-radius: 10px;
    
  }
  
  @keyframes loader {
    0% {
      transform: rotate(0deg);
    }
    
    25% {
      transform: rotate(180deg);
    }
    
    50% {
      transform: rotate(180deg);
    }
    
    75% {
      transform: rotate(360deg);
    }
    
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes loader-inner {
    0% {
      height: 0%;
    }
    
    25% {
      height: 0%;
    }
    
    50% {
      height: 100%;
    }
    
    75% {
      height: 100%;
    }
    
    100% {
      height: 0%;
    }
  }

#load-meme {
    margin-top: 50px;
    width: 400px;
    height: auto;
    border-radius: 15px;
}